export const datatable_config = {
    dtOptions : {
        pagingType: "full_numbers",
        columnDefs: [{
            orderable: false,
            className: "dt-center",
            targets: 0
        }], 
        order: [[ 1, 'desc' ]],
        language: {
            sSearch: "",
            searchPlaceholder: "Nhp thng tin tm kim",
            lengthMenu: "Hin th _MENU_ dng",
            sZeroRecords:  "Khng tm thy dng no ph hp",
            info: "Hin th _START_ n _END_ ca _TOTAL_",
            paginate: {
                'first': "u",
                'last': "Cui",
                'next': "Sau",
                'previous': "Trc"
            }
        },
        scrollX: true
    }
}
/* 
    Function: Customize dataTable
    @author: Trangle
*/ 
export let data_config = function(record) {
    return {
        record: record,
        dtOptions : {
            // Declare the use of the extension in the dom parameter
            language: {
                sSearch: '',
                searchPlaceholder: ' Nhp thng tin tm kim',
                lengthMenu: `Hin th _MENU_ ${record}`,
                info: `Hin th _START_ ti _END_ ca _TOTAL_ ${record}`,
                paginate: {
                    "first":      "u",
                    "last":       "Cui",
                    "next":       "Sau",
                    "previous":   "Trc"
                },
                select: {
                    rows: ''
                },
                sInfoFiltered: "",
                zeroRecords: `Khng c ${record} no  hin th`,
                infoEmpty: ""
            },
            responsive: true,
            pagingType: "full_numbers",
        }
    };
}


